*{
  background-color: rgb(223, 221, 215);
}
.header{
  height: 200px;
  width: 100%;
  background-color: #999999;
  padding-top: 50px;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  
}

.card-container{
  height: 300px;
  width: 300px;
  box-shadow: 0px 3px 50px #999999;
}

.card-wrapper{
  width: 270px; 
  height: 200px; 
  box-shadow: 0px 3px 10px #999999;
  display: flex;
  flex-direction: column;
  margin: auto;
  background-color: #999999;
}

.card-top{
  width: 100%;
  height: 2%;
}

.task-holder{
  width: 100%;
  height: 98%;
  padding: 10px 10px;
  display : flex;
  flex-direction: column;
  position: relative;
  
}

.card-header{
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 80px;
  height: 30px;
  padding: 1px 1px !important;
  text-align: center;
  font-size: 15px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.task-container{
  height: 100%;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  background-color: #000000;
  padding : 40px 100px;
  border-radius: 50px;
  margin: auto;
  
  
  
  
  
}
h3{
  background-color: #999999;
}
